/*Navbar Display CSS*/
.tiny_screen, .small_screen {
    display: none;
}

.large_screen {
    display: block;
}

/*Portfolio Preview Container CSS*/
.project_item {
    max-width: 50%;
}

/*Navbar Media Queries*/
@media (max-width: 830px) {
    .small_screen {
        display: block;
    }
    .large_screen {
        display: none;
    }
    #navbar_toggler {
        top: 123%;
    }
}

@media (max-width: 360px) {
    .small_screen {
        display: none;
    }
    .tiny_screen {
        display: block;
    }
    #navbar_toggler {
        top: 129%;
    }
}

@media (max-width: 767px) {
    .project_item {
        max-width: 100%;
    }
}


/*Project Tag Colours*/
.bg_cornflower_blue{background: #b3c7f7;}

.game{background: #b9e192;}

.web{background: #8babf1;}

.java{}

/*Project Pop-up/Overlay CSS*/
.fade_background {
    opacity:    0.5;
    background: #000;
    width:      100%;
    height:     100%;
    z-index:    10;
    top:        0;
    left:       0;
    position:   fixed;
}

.overlay {
    background: white;
    /*width: 80%;*/
    width: 100%;
    height: 100%;
    /*height: 80%;*/
    z-index: 10;
    /*top: 10%;*/
    top: 0;
    left: 0;
    /*left: 10%;*/
    position: fixed;
}

.image-container {
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}

.image-container.fade-out {
    opacity: 0;
}

@media (max-width: 576px){
    .remove_margin{
        margin: 0.5rem !important;
    }
}
